tainer .closeButton span:nth-child(2),#root .app .modalPreview .previewContainer .closeButton span:nth-child(2){-webkit-transform:rotate(-45deg);transform:rotate(-45deg);position:absolute}html .app .errorScreen,body .app .errorScreen,#root .app .errorScreen{display:flex;justify-content:center;align-items:center;position:fixed;top:0;right:0;bottom:0;left:0;background-color:#212121;z-index:2;display:none}html .app .errorScreen span,body .app .errorScreen span,#root .app .errorScreen span{color:#fff;font-size:20px;line-height:30px;max-width:80%;text-align:center}

